<template lang="html">
    <main-layout :settings-breadcrumbs="settingsBreadcrumbs">
        <template #content>
            <profile-form-layout
                :show-modal="showModal"
                @validate="validate"
                @close-modal="showModal=false"
                @confirm="confirm"
            >
                <template #form>
                    <div class="profile-info__form">
                        <div class="profile-info__form-group">
                            <div class="profile-info__form-row">
                                <form-radio-group
                                    class="profile-info__form--radio_group"
                                    :errors="validationMessage($v.paymethod)"
                                    @input.native="$v.paymethod.$touch()"
                                    @blur.native="$v.paymethod.$touch()"
                                >
                                    <!-- paypal -->
                                    <div
                                        id="paymentForm"
                                        class="checkout__payment checkout-paypal"
                                    >
                                        <form-input-radio
                                            v-model="$v.paymethod.$model"
                                            :field.sync="paymethod"
                                            :text="'PayPal'"
                                            :data="'paypal'"
                                            :is-checked="paymethod === 'paypal'"
                                            name="paypal"
                                        />
                                    </div>
                                    <!-- paypal -->

                                    <!-- payoneer -->
                                    <div class="checkout__payment checkout-credit">
                                        <form-input-radio
                                            v-model="$v.paymethod.$model"
                                            :field.sync="paymethod"
                                            :data="'payoneer'"
                                            :is-checked="paymethod === 'payoneer'"
                                            :text="'Payoneer.com Mastercard'"
                                            name="payoneer"
                                        />
                                    </div>
                                    <!-- payoneer -->
                                </form-radio-group>
                            </div>

                            <div
                                v-if="paymethod===`paypal`"
                                class="profile-info__form-row"
                            >
                                <div class="profile-info__col50">
                                    <form-input
                                        v-model="$v.email1.$model"
                                        legend="Email address"
                                        prepend-icon="EmailOutlineIcon"
                                        type="text"
                                        name="email"
                                        :field.sync="email1"
                                        :errors="validationMessage($v.email1)"
                                        :is-valid="$v.email1.$dirty && !$v.email1.$anyError"
                                        @input.native="$v.email1.$touch()"
                                        @blur.native="$v.email1.$touch()"
                                    />
                                </div>
                                <div class="profile-info__col50">
                                    <form-input
                                        v-model="$v.email2.$model"
                                        legend="Email address"
                                        prepend-icon="EmailOutlineIcon"
                                        type="text"
                                        name="email"
                                        :field.sync="email2"
                                        :errors="validationMessage($v.email2)"
                                        :is-valid="$v.email2.$dirty && !$v.email2.$anyError"
                                        @input.native="$v.email2.$touch()"
                                        @blur.native="$v.email2.$touch()"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </template>
            </profile-form-layout>
        </template>
    </main-layout>
</template>

<script>
import { eventBus } from '@/helpers/event-bus/'

import { validationMixin } from 'vuelidate'
import { validationMessage } from 'vuelidate-messages'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationRules } from '@/validation/profile/ProfilePaymentMethod/Rules'
import { formMessages } from '@/validation/profile/ProfilePaymentMethod/Messages'

import MainLayout from '@/layouts/MainLayout';
import ProfileFormLayout from '@/layouts/profile/ProfileFormLayout';

import { createNamespacedHelpers } from 'vuex';
import {
    ACTION_CLIENT_CHECK_EMAIL,
    ACTION_CLIENT_ASK_PASSWORD,
    ACTION_CLIENT_UPDATE_PAYMETHOD
} from '@/store/modules/client/action-types';
import {
    mapClientFields
} from '@/store/modules/client';
import FormRadioGroup from '@/components/common/form-elements/FormRadioGroup';
import FormInputRadio from '@/components/common/form-elements/FormInputRadio';

const {
    mapActions: mapClientActions
} = createNamespacedHelpers('client');

export default {
    components: {
        FormInputRadio,
        FormRadioGroup,
        MainLayout,
        ProfileFormLayout
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data() {
        return {
            paymethod: 'paypal',
            email1: '',
            email2: '',
            showModal: false,
            settingsBreadcrumbs: {
                title: 'Withdrawal Method',
                steps: [
                    { url: '/', title: 'Home' },
                    { url: '/cp/account', title: 'Account' },
                    { url: '/cp/profile/payment-method', title: 'Change Withdrawal Method' }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            }
        }
    },
    computed: {
        ...mapClientFields([
            'client_data.aff_paymethod',
            'client_data.aff_paypal_email',
            'client_data.email'
        ]),
        isPaypal() {
            return this.paymethod === 'paypal'
        },
        defaultEmail() {
            return this.isPaypal ? this.email1 : this.email
        }
    },
    watch: {
        aff_paymethod(value) {
            if (value === 'paypal') {
                this.email1 = ''
                this.email2 = ''
            }
        }
    },
    mounted() {
        eventBus.$on('saveSettings', () => {
            this.$v.$reset()
            this.validate()
        })
    },
    beforeDestroy() {
        eventBus.$off('saveSettings')
    },
    methods: {
        validationMessage: validationMessage(formMessages),
        ...mapClientActions({
            checkEmail: ACTION_CLIENT_CHECK_EMAIL,
            askForPassword: ACTION_CLIENT_ASK_PASSWORD,
            updatePaymethod: ACTION_CLIENT_UPDATE_PAYMETHOD
        }),
        confirm() {
            try {
                this.updatePaymethod({
                    paymethod: this.paymethod,
                    email: this.defaultEmail
                })
                    .then(() => {
                        eventBus.$emit('showSnackBar', 'Your settings have been changed', 'success');
                        this.$router.push({ name: 'profile' })
                    })
            } catch (err) {
                console.log(err)
            }
        },
        submit() {
            this.showModal = true
        }
    }
}
</script>

<style lang="scss">
    .profile-info__form--radio_group {
        display: flex;
        .checkout__payment:nth-child(1) {
            margin-right: 20px;
        }
    }
    @media screen and (max-width: 450px){
        .profile-info__form--radio_group {
            flex-wrap: wrap;
            .checkout__payment {
                width: 100%;
            }
            .checkout__payment:nth-child(1) {
                margin-right: 0px;
                margin-bottom: 20px;
            }
        }
    }
</style>
