<template lang="html">
    <main-layout :settings-breadcrumbs="settingsBreadcrumbs">
        <template #content>
            <profile-form-layout
                :show-modal="showModal"
                @validate="validate"
                @close-modal="showModal=false"
                @confirm="confirm"
            >
                <template #headerIcon>
                    <email-outline />
                </template>
                <template #form>
                    <div class="profile-info__form mt-7">
                        <div class="profile-info__form-group">
                            <div class="profile-info__form-row">
                                <div class="profile-info__col50">
                                    <form-input
                                        v-model="emailValue"
                                        legend="Email address"
                                        prepend-icon="EmailOutlineIcon"
                                        type="text"
                                        name="email"
                                        :field.sync="emailValue"
                                        :errors="validationMessage($v.email)"
                                        :is-valid="$v.email.$dirty && !$v.email.$anyError"
                                        @input.native="$v.email.$touch()"
                                        @blur.native="$v.email.$touch()"
                                    />
                                </div>
                                <div class="profile-info__col50">
                                    <form-input
                                        v-model="$v.email2.$model"
                                        legend="Email address"
                                        prepend-icon="EmailOutlineIcon"
                                        type="text"
                                        name="email"
                                        :field.sync="email2"
                                        :errors="validationMessage($v.email2)"
                                        :is-valid="$v.email2.$dirty && !$v.email2.$anyError"
                                        @input.native="$v.email2.$touch()"
                                        @blur.native="$v.email2.$touch()"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </template>
            </profile-form-layout>
        </template>
    </main-layout>
</template>

<script>
import { eventBus } from '@/helpers/event-bus/'

import MainLayout from '@/layouts/MainLayout';
import ProfileFormLayout from '@/layouts/profile/ProfileFormLayout';
import EmailOutline from 'mdi-vue/EmailOutline'
import { validationMixin } from 'vuelidate'
import { validationMessage } from 'vuelidate-messages'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationRules } from '@/validation/profile/ProfileEmail/Rules'
import { formMessages } from '@/validation/profile/ProfileEmail/Messages'

import { createNamespacedHelpers } from 'vuex';
import {
    ACTION_CLIENT_UPDATE_EMAIL,
    ACTION_CLIENT_CHECK_EMAIL
} from '@/store/modules/client/action-types';
import {
    UPDATE_CLIENT_EMAIL
} from '@/store/modules/client/mutation-types';
import { debounce } from 'lodash'

const {
    mapActions: mapClientActions,
    mapMutations: mapClientMutations
} = createNamespacedHelpers('client');

export default {
    components: {
        MainLayout,
        ProfileFormLayout,
        EmailOutline
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data() {
        return {
            emailValue: '',
            email: '',
            email2: '',
            showModal: false,
            settingsBreadcrumbs: {
                title: 'Email',
                steps: [
                    { url: '/', title: 'Home' },
                    { url: '/cp/account', title: 'Account' },
                    { url: '/cp/profile/email', title: 'Change Email' }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            }
        }
    },
    watch: {
        emailValue: debounce(function (newEmail) {
            this.email = newEmail
        }, 300)
    },
    mounted() {
        eventBus.$on('saveSettings', () => {
            this.$v.$reset()
            this.validate()
        })
    },
    beforeDestroy() {
        eventBus.$off('saveSettings')
    },
    methods: {
        validationMessage: validationMessage(formMessages),
        ...mapClientActions({
            updateEmail: ACTION_CLIENT_UPDATE_EMAIL,
            clientCheckEmail: ACTION_CLIENT_CHECK_EMAIL
        }),
        ...mapClientMutations([
            UPDATE_CLIENT_EMAIL
        ]),
        async checkEmail(value) {
            await this.clientCheckEmail(value)
        },
        confirm() {
            try {
                const response = this.updateEmail({
                    email: this.email
                })
                if (response) {
                    eventBus.$emit('showSnackBar', 'Your e-mail has been changed', 'success');
                    this[UPDATE_CLIENT_EMAIL](this.email)
                    this.$router.push({ name: 'profile' })
                }
            } catch (err) {
                console.log(err)
            }
        },
        submit() {
            this.showModal = true
        }
    }
}
</script>

<style lang="scss" scoped>
.profile-info__form-row {
    .profile-info__col50 {
        margin-top: 0 !important;
    }
}
@media screen and (max-width: 768px){
    .profile-info__form-row {
        .profile-info__col50:nth-child(2) {
            margin-top: 24px !important;
        }
    }
}
</style>
